import React, { useEffect } from "react";
import { connect } from "react-redux";

let route = {};
function ClimbingRoute({ match, route }) {
  const id = match.params.id;
  function getRouteImage(id) {
    return "url('https://previews.123rf.com/images/izanbar/izanbar1512/izanbar151200178/49044826-indoor-climbing-gym-wall-detail-close-up.jpg')";
  }
  return (
    <div className="route-container">
      <div className="route-holds-list">
        <ul>
          {route.holds.map(item => (
            <li key={item.id}>{item.id}</li>
          ))}
        </ul>
      </div>
      <div
        className="route-full-img"
        style={{ backgroundImage: getRouteImage(route.id) }}
      >
        <ul>
          {route.holds.map(item => {
            if (item.isActive === true) {
              return (
                <div
                  key={item.id}
                  className="route-hold-marker"
                  style={{ position: "absolute", left: item.x, top: item.y,
                  borderColor: "green" }}
                />
              );
            } else {
              return (
                <div
                  key={item.id}
                  className="route-hold-marker"
                  style={{
                    position: "absolute",
                    left: item.x,
                    top: item.y
                  }}
                />
              );
            }
          })}
        </ul>
      </div>
    </div>
  );
}

function mapStateToProps(state, ownProps) {
  const id = ownProps.match.params.id;
  route = state.routes.find(item => item.id === id);

  // component receives additionally:
  return { route };
}
// const mapStateToProps = ({state,ownProps}) => ({
//     route: state.routes.find(item=>item.id===ownProps.id)
//   });

const mapDispatchToProps = dispatch => ({
  //toggleTodo: id => dispatch(toggleTodo(id))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ClimbingRoute);

