import React from "react";
import SimpleRouteCard from "./SimpleRouteCard";
import { connect } from "react-redux";

function ClimbingRoutes({ routes }) {
  return (
    <ul className="routesList">
      {routes.map(item => (
            <SimpleRouteCard
            key={item.id}
              routeInfo={item}
            />
      ))}
    </ul>
  );
}
const mapStateToProps = state => ({
  routes: state.routes
});

const mapDispatchToProps = dispatch => ({
  //toggleTodo: id => dispatch(toggleTodo(id))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ClimbingRoutes);
//export default withStyles(styles)(ClimbingRoutes);
