import React from "react";

import { Route } from "react-router-dom";
const SimpleRouteCard = ({ routeInfo }) => {
  function getThumbnailImage(imageId) {
    return "url('https://previews.123rf.com/images/izanbar/izanbar1512/izanbar151200178/49044826-indoor-climbing-gym-wall-detail-close-up.jpg')";
  }
  function isRouteAvailable(isActive) {
    if (isActive) {
      return { pointerEvents: "none", opacity: 0.4 };
    }
    return {};
  }
  return (
    <Route
      render={({ history }) => (
        <div
          className="route-card"
          style={isRouteAvailable(routeInfo.isActive)}
          onClick={() => {
            history.push("route/" + routeInfo.id);
          }}
        >
          <div
            className="route-card-img"
            style={{ backgroundImage: getThumbnailImage(routeInfo.picture) }}
          />
          <div className="route-card-title">{routeInfo.title}</div>
          <div className="route-card-description">{routeInfo.description}</div>
          <div className="route-card-level">
            {"Route Rank: " + routeInfo.level}
          </div>
        </div>
      )}
    />
  );
};
export default SimpleRouteCard;
